<script async setup>
    import { ref } from 'vue'
    import { storeToRefs } from 'pinia'

    import {
        useSepaCreditTransferStore,
        useSepaConfigStore,
    } from '@/stores/sepa_credit_transfer'
    import Table from '@/components/lists/Table.vue'
    import PaginationWidget from '@/components/PaginationWidget.vue'
    import { collectOptions } from '@/helpers/context'
    import Icon from '@/components/Icon.vue'

    import columnsDef from './columnsDef'

    const loading = ref(true)

    const options = collectOptions()
    const store = useSepaCreditTransferStore()
    const configStore = useSepaConfigStore()
    configStore.setUrl(options.form_config_url)

    const {
        creditTransferCollection,
        creditTransferLoadOptions,
        creditTransferCollectionMeta,
    } = storeToRefs(store)

    const params = ref({
        sort: creditTransferLoadOptions.value.sort,
        columnsDef: columnsDef,
        columns: columnsDef.map((column) => column.name),
    })

    const onSort = (column, sortDirection) => {
        creditTransferLoadOptions.value.sort.sort = column
        creditTransferLoadOptions.value.sort.sortDirection = sortDirection
    }

    async function preload() {
        const promise1 = store.setInitialValues(options.credit_transfer_id)
        const promise2 = configStore.loadConfig()
        const promise3 = store.loadCreditTransferCollection()

        await Promise.all([promise1, promise2, promise3])
        loading.value = false
    }
    preload()
</script>
<template>
    <div v-if="loading">Chargement des informations</div>
    <div v-else>
        <div class="main_toolbar action_tools">
            <div class="layout flex main_actions">
                <div role="group">
                    <RouterLink to="/" class="btn icon only">
                        <Icon name="arrow-left" />
                        &nbsp;Revenir à la liste des règlements en attente
                    </RouterLink>
                </div>
            </div>
        </div>

        <h1>Historique des virements SEPA</h1>

        <template v-if="creditTransferCollectionMeta.total_count === 0">
            <div class="alert alert-info">
                Aucun virement SEPA n'a été effectué.
            </div>
        </template>
        <template v-else>
            <PaginationWidget
                v-model:page="creditTransferLoadOptions.pagination.page"
                v-model:items-per-page="
                    creditTransferLoadOptions.pagination.per_page
                "
                v-model:pagination="creditTransferLoadOptions.pagination"
                :total-pages="creditTransferCollectionMeta.total_pages"
                :total-count="creditTransferCollectionMeta.total_count"
            />
            <Table
                :items="creditTransferCollection"
                :params="params"
                @sort-by="onSort"
            >
                <template #rowActions="{ item }">
                    <td class="col_actions width_two">
                        <div class="btn-group">
                            <RouterLink
                                class="btn icon only"
                                :to="`/orders/${item.id}`"
                                title="Voir le détail de l'ordre de virement"
                                aria-label="Afficher le détail de l'ordre de virement"
                            >
                                <Icon name="eye" /> Voir
                            </RouterLink>
                            <a
                                class="btn icon only"
                                :href="`/files/${item.file_id}?action=download`"
                                title="Télécharger le fichier XML de l'ordre de virement"
                                aria-label="Ouvre une nouvelle fenêtre pour télécharger le fichier XML de l'ordre de virement"
                                target="_blank"
                            >
                                <Icon name="download" /> Télécharger le fichier
                            </a>
                        </div>
                    </td>
                </template>
            </Table>
        </template>
    </div>
</template>
