<script setup>
    import { computed } from 'vue'
    const props = defineProps({
        item: { type: Object, required: true },
        getValue: { type: Function, required: false },
        name: { type: String, required: true },
    })
    const user = computed(() =>
        props.getValue ? props.getValue(props.item) : props.item[props.name]
    )
</script>
<template>
    <td class="col_text">
        <template v-if="user">
            {{ user.label }}
        </template>
    </td>
</template>
